import { createHTMLElement, getTypeStyles } from "../functions/functions";
import pokeball from "../assets/modal/pokeball.svg";
import heighticon from "../assets/modal/straighten.svg";
import weighticon from "../assets/modal/weight.svg";

export const openModal = (pokemon) => {
  const main = document.querySelector("main");
  console.log(pokemon);
  const div = createHTMLElement("div", [
    "flex",
    "flex-col",
    "justify-center",
    "items-center",
    "w-full",
    "h-full",
    "bg-gray-200",
    "bg-opacity-80",
    "fixed",
    "top-0",
    "right-0",
  ]);
  const divModal = createHTMLElement("div", [
    "w-3/5",
    "h-4/5",
    "flex",
    "flex-col",
    "justify-between",
    "items-center",
    "border-2",
    "border-solid",
    "rounded-lg",
    `border-${getTypeStyles(pokemon.types[0].type.name)}`,
    `shadow-${getTypeStyles(pokemon.types[0].type.name)}`,
    "shadow-xl",
    "gap-2",
    "py-2",
    "shadow-2xl",
    `bg-${getTypeStyles(pokemon.types[0].type.name)}`,
    "relative",
    "bg-[url(" + pokeball + ")]",
    "bg-opacity-70",
  ]);
  const closeButton = createHTMLElement("h2", [
    "absolute",
    "top-2",
    "right-4",
    "text-red-600",
    "text-2xl",
    "cursor-pointer",
  ]);

  const pokeballIcon = createHTMLElement("img", [
    "absolute",
    "top-2",
    "right-4",
    "w-16",
    "h-16",
  ]);

  closeButton.textContent = "X";

  closeButton.addEventListener("click", () => {
    div.remove();
  });

  const name = createHTMLElement("h1", [
    "font-bold",
    "text-2xl",
    "text-center",
    "uppercase",
    "text-white",
  ]);

  name.textContent = pokemon.name;

  const img = createHTMLElement("img", [
    "drop-shadow-md",
    "h-1/3",
    "absolute",
    "top-2",
  ]);
  img.src = pokemon.sprites.other.home.front_default;
  img.alt = pokemon.name;

  const weigth = createHTMLElement("h2");
  weigth.textContent = `${pokemon.weight} gr`;
  const height = createHTMLElement("h2");
  height.textContent = `${pokemon.height} cm`;

  const divTypes = createHTMLElement("div", [
    "flex",
    "justify-center",
    "items-center",
    "gap-2",
  ]);

  const types = pokemon.types.map((type) => type.type.name);
  const type = types.map((type) => {
    const divType = createHTMLElement("div", [
      "shadow-2xl",
      `border-${getTypeStyles(type)}`,
      `shadow-${getTypeStyles(type)}`,
    ]);
    const h2 = createHTMLElement("h2", [
      "w-20",
      "font-bold",
      "text-center",
      "uppercase",
      "text-white",
      "rounded-lg",
      `bg-${getTypeStyles(type)}`,
    ]);
    div.classList.add(
      `border-${getTypeStyles(type)}`,
      `shadow-${getTypeStyles(type)}`
    );

    h2.textContent = type;
    divType.append(h2);
    return divType;
  });

  divTypes.append(...type);

  main.append(div);
  div.append(divModal);
  divModal.append(closeButton, name, img);

  const divInfo = createHTMLElement("div", [
    "flex",
    "flex-col",
    "gap-2",
    "items-center",
    "justify-center",
    "w-4/5",
    "bg-white",
    "h-3/4",
    "border-2",
    "border-solid",
    "shadow-2xl",
    "shadow-white",
    "rounded-lg",
  ]);

  const divAtributes = createHTMLElement("div", []);
  const divHeight = createHTMLElement("div", []);
  const divWeight = createHTMLElement("div", []);
  const abilities = createHTMLElement("div", []);

  const allAbilities = pokemon.abilities.map((ability) => {
    const divAbility = createHTMLElement("div", [
      "shadow-2xl",
      "border-black",
      "shadow-black",
      "border-2",
      "border-solid",
      "rounded-lg",
      "gap-2",
      "py-2",
      "cursor-pointer",
      "shadow-2xl",
    ]);
    const h2 = createHTMLElement("h2", [
      "w-20",
      "font-bold",
      "text-center",
      "uppercase",
      "text-white",
      "rounded-lg",
      `bg-${getTypeStyles(type)}`,
    ]);
    h2.textContent = type;
    divAbility.append(h2);
    return divAbility;
  });

  divModal.append(divInfo);
  divInfo.append(divTypes, divAtributes);
  divAtributes.append(divWeight, divHeight, abilities);
  abilities.append(...allAbilities);
};
